.main-screen {
  width: 100%;
  height: 100vh;
  min-height: 700px;
  max-height: 1080px;
  @media (max-width: 480px) {
    min-height: 500px;
  }
  position: relative;
  &_block {
    background-color: #cfcfcf;
    width: 100%;
    height: 100%;
    position: relative;
    padding: 13.55% 8.33%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    overflow: hidden;
    @media (max-width: 1500px) {
      padding: 10% 5%;
    }
    @media (max-width: 1300px) {
      padding: 150px 30px;
    }
    @media (max-width: 768px) {
      padding-top: 140px;
      align-items: flex-start;
    }
    @media (max-width: 480px) {
      padding-top: 120px;
      justify-content: center;
    }
    &:after {
      content: '';
      width: 100%;
      height: 30%;
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      background-image: linear-gradient(to bottom, #fff, transparent);
    }
  }
  &_img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
    object-position: center;
    transition: transform .7s ease-out;
    transform: scale(1.2);
    &.view {
      transform: scale(1);
    }
    &.mob {
      display: none;
      @media (max-width: 768px) {
        display: block;
        object-position: left;
      }
    }
    &.desk {
      @media (max-width: 768px) {
        display: none;
      }
    }
  }
  &_subtitle {
    color: #fff;
    font-weight: 300;
    display: block;
    margin-bottom: 30px;
    font-size: 2.25rem;
    line-height: 60px;
    text-shadow: 0px 0px 15px rgba(0,0,0,0.5);
    max-width: 420px;
    padding-left: 80px;
    opacity: 0;
    transition: opacity .25s ease-out 0.1s,transform .25s ease-out 0.1s;
    transform: translateY(40px);
    @media (max-width: 1024px) {
      padding-left: 0;
      line-height: 44px;
    }
    @media (max-width: 480px) {
      font-size: 2rem;
      line-height: 2.25rem;
      width: 80%;
      margin-bottom: 20px;
    }
    @media (max-width: 360px) {
      margin-bottom: 10px;
      font-size: 1.5rem;
    }
    &.view {
      opacity: 1;
      transform: translateY(0);
    }
  }
}
.logo {
  width: 844px;
  opacity: 0;
  transition: opacity .4s ease-out 0.3s,transform .4s ease-out 0.3s;
  transform: translateY(40px);
  &.view {
    opacity: 1;
    transform: translateY(0);
  }
  @media (max-width: 1024px) {
    width: 500px;
  }
  @media (max-width: 768px) {
    width: 400px;
  }
  @media (max-width: 480px) {
    width: 100%;
  }
}

.scroll {
  width: 32px;
  height: 32px;
  background-image: url(../images/sprite.svg#arrow-down_white);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  bottom: 80px;
  left: 50%;
  margin-left: -16px;
  opacity: 0;
  transform: translateY(-10px);
  transition: all 0.3s ease 0.9s;
  animation: scroll 2s infinite 1s;
  &.view {
    opacity: 1;
    transform: translateY(0px);
  }
  @media (max-width: 1400px) {
    bottom: 30px;
  }
  @media (max-width: 480px) {
    width: 22px;
    height: 22px;
    margin-left: -11px;
  }
}


@keyframes scroll {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  30% {
    opacity: 1;
  }
  70% {
    opacity: 1;
  }
  100% {
    transform: translateY(0px);
    opacity: 0;
  }
}

.ms-slider {
  width: 100%;
  height: 100%;
  &_list {
    width: 100%;
    height: 100%;
  }
  &_item {
    display: none;
    height: 100%;
    &:first-child {
      display: block;
    }
  }
  .slider-controls {
    position: absolute;
    bottom: 80px;
    left: 12.1%;
    opacity: 0;
    transition: all 0.3s ease 0.9s;
    &.view {
      opacity: 1;
    }
    @media (max-width: 1400px) {
      bottom: 30px;
    }
    @media (max-width: 1024px) {
      left: 30px;
    }
    @media (max-width: 480px) {
      display: none;
    }
  }
  .slick-dots {
    margin-top: 0!important;
  }
}
.slick-list,.slick-track {
  height: 100%;
}

.slider-controls {
  display: flex;
  align-items: center;
}
.slider-dots {
  margin-right: 40px;
}
.arrow-prev, .arrow-next {
  width: 15px;
  height: 29px;
  background-image: url(../images/sprite.svg#arrow-prev_white);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-right: 40px;
  &:hover {
    background-image: url(../images/sprite.svg#arrow-prev_blue);
  }
}
.arrow-next {
  transform: rotate(180deg);
}

.arrow-prev-gray, .arrow-next-gray {
  width: 15px;
  height: 29px;
  background-image: url(../images/sprite.svg#arrow-prev_gray);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-right: 40px;
  @media (max-width: 480px) {
    width: 12px;
    height: 20px;
    margin-right: 30px;
  }
  &:hover {
    background-image: url(../images/sprite.svg#arrow-prev_black);
  }
}
.arrow-next-gray {
  transform: rotate(180deg);
}
.slider-dots-gray {
  margin-right: 40px;
  @media (max-width: 480px) {
    margin-right: 30px;
  }
  .slick-dots {
    li {
      background-color: #c4c4c4;
      border-color: #c4c4c4;
      &.slick-active {
        background: #000;
        border-color: #000;
      }
    }
  }
}











