.card {
  width: 33%;
  position: relative;
  overflow: hidden;
  padding: 20px;
  display: block;
  text-decoration: none;
  color: #000;
  @media (max-width: 1300px) {
    padding: 10px;
  }
  &:after {
    content: '';
    width: 100%;
    height: 100%;
    max-height: 0;
    background-image: linear-gradient(to top, #0072A5 52%, transparent 80%);
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    opacity: 0;
    transition: all 0.1s ease;
  }
  &:hover {
    @media (min-width: 768px) {
      color: #fff;
      &:after {
        max-height: 1000px;
        opacity: 1;
      }
      .card_thin-title,
      .card_color-title,
      .card_description {
        color: #fff;
      }
      .card_img {
        transform: scale(1.2);
      }
      .link {
        opacity: 1;
        transform: translateX(0px);
        transition: all 0.3s ease;
      }
    }
  }
  &_img {
    transition: all 0.2s ease;
    width: 100%;
    height: 290px;
    @media (max-width: 480px) {
      height: 200px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
  &_text {
    padding: 30px 20px 70px 20px;
    position: relative;
    z-index: 5;
    transition: all 0.2s ease;
    @media (max-width: 480px) {
      padding: 20px 20px 36px 20px;
    }
  }
  &_title {
    font-size: 1.5rem;
    text-transform: uppercase;
    margin-bottom: 35px;
  }
  &_thin-title {
    font-size: 0.875rem;
    font-weight: 300;
    color: #757575;
    display: block;
    margin-bottom: 5px;
  }
  &_color-title {
    color: $accent;
    font-weight: 500;

  }
  &_description {
    height: 200px;
    overflow: hidden;
    color: #757575;
    font-weight: 300;
    font-size: 1.125rem;
    line-height: 1.375rem;
  }
  table {
    border-collapse: collapse;
    @media (max-width: 480px) {
      width: 100%;
    }
    td {
      padding: 10px 14px;
      border-bottom: 1px solid rgba(63,172,220,0.3);
      @media (max-width: 1300px) {
        padding: 10px 5px;
      }
      &:first-child {
        padding-left: 0;
      }
    }
  }
  .link {
    position: absolute;
    bottom: 20px;
    right: 20px;
    opacity: 0;
    transform: translateX(-20px);
    @media (max-width: 768px) {
      opacity: 1;
      transform: translateX(0px);
    }
    @media (max-width: 480px) {
      bottom: 10px;
    }
    &:hover {
      color: #3299c7;
      &:after {
        background-image: url(../images/arrow-right.svg);
      }
    }
  }
}

.link {
