.letter-a {
  position: absolute;
  top: 1750px;
  right: 60px;
}
.letter-s {
  position: absolute;
  top: 2430px;
  left: 100px;
}
.letter-u {
  position: absolute;
  top: 3420px;
  right: 80px;
}
.letter-t {
  position: absolute;
  top: 4690px;
  right: 50%;
  margin-right: -165px;
  @media (max-width: 1600px) {
    top: 4500px;
  }
}
.letter-a_2 {
  position: absolute;
  top: 6500px;
  right: 0px;
}
.letter-l {
  position: absolute;
  top: 7600px;
  left: 50px;
  @media (max-width: 1660px) {
    height: 200px;
    top: 7550px;
  }
  @media (max-width: 1460px) {
