* {
	outline: none;
	box-sizing: border-box;
}
*::before, *::after {
	box-sizing: border-box;
}
html {
	font-size: 16px;
	@media (max-width: 1024px) {
		font-size: 15px;
	}
	@media (max-width: 768px) {
		font-size: 14px;
	}
	@media (max-width: 480px) {
		font-size: 12px;
	}
}
body {
	background-color: #fff;
	font-family: 'Proxima Nova', sans-serif;
	margin: 0;
	min-height: 100vh;
	scroll-behavior: smooth;
	font-size: 1rem;
	display: flex;
	flex-direction: column;
	overflow-x: hidden;
}
.main-inner {
	flex-grow: 1;

}
ul[class], ol[class] {
	list-style: none;
	padding: 0;
	margin: 0;
}
h1[class], h2[class], h3[class], h4[class], h5[class] {
	margin-top: 0;
}
.uppercase {
	text-transform: uppercase;
}
img {
	max-width: 100%;
	display: block;
}
input, button, textarea, select {
	font: inherit;
}

.container {
	max-width: $container;
	width: 100%;
	margin: 0 auto;
}
.content-wrap {
  max-width: $content;
  margin: 0 auto;
	width: 100%;
	@media (max-width: 1500px) {
		padding-left: 20px;
		padding-right: 20px;
	}
	// @media (max-width: 1200px) {
	// 	padding: 0 10px;
	// }
}
.no-scroll {
	overflow: hidden;
}

.link {
  color: $accent;
  &:hover {
    text-decoration: none;
  }
}

.slick-dots {
	display: flex;
	justify-content: center;
	margin-top: 50px!important;
	@media (max-width: 768px) {
		margin-top: 30px!important;
	}
	@media (max-width: 480px) {
		margin-top: 15px!important;
	}
	li {
		width: 11px;
		height: 11px;
		background-color: #DAE0E3;
		margin-right: 22px;
		border-radius: 50%;
		cursor: pointer;
		&:last-child {
			margin-right: 0;
		}
		&.slick-active {
			background: $accent;
		}
		@media (max-width: 768px) {
			width: 8px;
			height: 8px;
			margin-right: 12px;
		}
		@media (max-width: 480px) {
			width: 6px;
			height: 6px;
			margin-right: 8px;
		}
	}
	button {
		font-size: 0;
		line-height: 0;
		padding: 0;
		display: none;
	}
}
