.footer {
  background: #0D6992;
  color: #fff;
  .content-wrap {
    @media (max-width: 1500px) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}
.footer-top {
  padding-top: 100px;
  padding-bottom: 70px;
  display: flex;
  @media (max-width: 480px) {
    display: block;
    padding-top: 70px;
    padding-bottom: 50px;
  }
}

.f-contacts {
  padding-right: 200px;
  font-weight: 500;
  font-size: 1.125rem;
  @media (max-width: 1200px) {
    padding-right: 100px;
  }
  @media (max-width: 768px) {
    padding-right: 50px;
  }
  @media (max-width: 480px) {
    padding-right: 0;
  }
  &_item {
    display: block;
    margin-bottom: 10px;
    color: #fff;
    text-decoration: none;
  }
}

.f-nav {
  column-count: 2;
  @media (max-width: 768px) {
    column-count: 1;
  }
  @media (max-width: 480px) {
    display: none;
  }
  &_link {
    color: rgba(255,255,255,0.7);
    text-decoration: none;
    font-size: 14px;
    font-weight: 300;
    display: block;
    margin-bottom: 18px;
    padding-right: 100px;
    @media (max-width: 1024px) {
      padding-right: 20px;
    }
    @media (max-width: 768px) {
      margin-bottom: 10px;
      padding-right: 0;
    }
    &:hover {
      text-decoration: underline;
    }
  }
}

.footer-bottom {
  background-color: #10638C;
  &_wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 25px;
    padding-bottom: 35px;
  }
}
.f-logo {
  img {
    width: 120px;
  }
}
.f-social-icons {
  display: flex;
  margin-top: 30px;
}
.f-social-icon {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #fff;
  display: block;
  margin-right: 16px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  &:hover {
    background-color: rgba(255,255,255,.8);
  }
  @media (max-width: 480px) {
    width: 20px;
    height: 20px;
    margin-right: 8px;
  }
  &:last-child {
    margin-right: 0;
  }
  &:before {
    content: '';
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: block;
  }
  &.inst {
    &:before {
      width: 16px;
      height: 16px;
      background-image: url(../images/sprite.svg#inst_blue);
      @media (max-width: 480px) {
        width: 10px;
        height: 10px;
      }
    }
  }
  &.youtube:before {
    width: 16px;
    height: 13px;
    background-image: url(../images/sprite.svg#youtube_blue);
    @media (max-width: 480px) {
      width: 10px;
      height: 8px;
    }
  }
}
