.inner-head {
  background-color: #036590;
  background-size: cover;
  background-position: center;
  height: 523px;
  position: relative;
  @media (max-width: 1024px) {
    height: 400px;
  }
  @media (max-width: 768px) {
    height: 350px;
  }
  &_title {
    color: #fff;
    font-weight: 500;
    font-size: 96px;
    background-image: url(../images/inner-head-title_bg.png);
    background-size: cover;
    padding: 20px 80px 20px 210px;
    display: block;
    position: absolute;
    bottom: 110px;
    left: 0;
    margin-bottom: 0;
    transform: translate3d(0,-100px,0);
    opacity: 0;
    transition: all .4s ease-out;
    &.view {
      opacity: 1;
      transform: translateZ(0);
    }
    @media (max-width: 1400px) {
      padding-left: 140px;
    }
    @media (max-width: 1024px) {
      font-size: 70px;
      padding-left: 80px;
      bottom: 80px;
    }
    @media (max-width: 768px) {
      font-size: 50px;
      bottom: 50px;
    }
    @media (max-width: 480px) {
      font-size: 38px;
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}

.breadcrumbs {
  padding-top: 30px;
  padding-left: 210px;
  @media (max-width: 1400px) {
    padding-left: 140px;
  }
  @media (max-width: 1024px) {
    padding-left: 40px;
  }
  @media (max-width: 480px) {
    padding-left: 20px;
  }
  &_item {
    color: #fff;
    text-decoration: none;
    font-size: 13px;
    line-height: 13px;
    padding-right: 20px;
    margin-right: 20px;
    position: relative;
    &:after {
      content: '';
      width: 1px;
      height: 10px;
      background-color: #fff;
      display: block;
      position: absolute;
      right: 0;
      top: 3px;
    }
    &:last-child {
      color: $accent;
      pointer-events: none;
      &:after {
        display: none;
      }
    }
  }
}

.step-wrap {
  position: relative;
  &:after {
    content: '';
    width: 40px;
    height: 40px;
    display: block;
    background-image: url(../images/pin.gif);
    background-size: cover;
    position: absolute;
    @media (max-width: 480px) {
      width: 20px;
      height: 20px;
    }
  }
  &:before {
    content: '';
    width: 2px;
  }
  &.step_01 {
    &:before {
      height: 70%;
      display: block;
      border-right: 1px dashed #68CEFC;
      position: absolute;
      left: 48px;
      bottom: 0;
      @media (max-width: 1500px) {
        left: 23px;
      }
      @media (max-width: 1300px) {
        height: 86%;
      }
      @media (max-width: 900px) {
        height: 82%;
      }
      @media (max-width: 480px) {
        left: 13px;
        height: 86%;
      }
    }
    &:after {
      left: 30px;
      top: 250px;
      @media (max-width: 1500px) {
        left: 5px;
      }
      @media (max-width: 1300px) {
        top: 185px;
      }
      @media (max-width: 768px) {
        top: 160px;
      }
      @media (max-width: 480px) {
        top: 120px;
      }
    }
  }
  &.step_02 {
    &:before {
      height: 100%;
      display: block;
      border-right: 1px dashed #68CEFC;
      position: absolute;
      left: 49px;
      bottom: 0;
      @media (max-width: 1500px) {
        left: 23px;
      }
      @media (max-width: 480px) {
        left: 13px;
      }
    }
    &:after {
      background-color: #fff;
      left: 30px;
      top: 262px;
      @media (max-width: 1500px) {
        left: 5px;
      }
      @media (max-width: 1300px) {
        top: 185px;
      }
      @media (max-width: 768px) {
        top: 160px;
      }
      @media (max-width: 480px) {
        top: 120px;
      }
    }
  }
  &.step_03 {
    &:before {
      height: 200px;
      display: block;
      border-right: 1px dashed #68CEFC;
      position: absolute;
      left: 49px;
      top: 0;
      @media (max-width: 1500px) {
        left: 23px;
      }
      @media (max-width: 1300px) {
        height: 176px;
      }
      @media (max-width: 768px) {
        height: 150px;
      }
      @media (max-width: 480px) {
        left: 13px;
        height: 115px;
      }
    }
    &:after {
      left: 30px;
      top: 210px;
      @media (max-width: 1500px) {
        left: 5px;
      }
      @media (max-width: 1300px) {
        top: 185px;
      }
      @media (max-width: 768px) {
        top: 160px;
      }
      @media (max-width: 480px) {
        top: 120px;
      }
    }
  }
}

.step {
  padding: 130px 20px 130px 150px;
  background-image: url(../images/services_bg.png);
  background-position: right top;
  background-repeat: repeat-y;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  @media (max-width: 1700px) {
    padding-right: 0;
  }
  @media (max-width: 1500px) {
    padding-left: 90px;
  }
  @media (max-width: 1300px) {
    display: block;
  }
  @media (max-width: 768px) {
    padding-right: 20px;
    padding-left: 60px;
    background-image: none;
  }
  @media (max-width: 480px) {
    padding-left: 35px;
    padding-top: 80px;
    padding-bottom: 80px;
  }
  &_text {
    width: 50%;
    padding-right: 80px;
    opacity: 0;
    transform: translateX(-40px);
    transition: all 0.4s ease;
    &.view {
      opacity: 1;
      transform: translateX(0px);
    }
    @media (max-width: 1600px) {
      padding-right: 30px;
    }
    @media (max-width: 1300px) {
      width: 100%;
      margin-bottom: 60px;
    }
    @media (max-width: 768px) {
      padding-right: 9px;
    }
    @media (max-width: 480px) {
      padding-right: 0;
    }
  }
  &_img {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 1300px) {
      width: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &_number {
    color: #116893;
    font-size: 120px;
    font-weight: 500;
    margin-right: 40px;
    display: block;
    @media (max-width: 768px) {
      font-size: 80px;
      margin-right: 20px;
    }
  }
  &_title {
    font-weight: bold;
    font-size: 2.25rem;
    @media (max-width: 768px) {
      font-size: 2rem;
    }
  }
  &_head {
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    @media (max-width: 480px) {
      display: block;
    }
  }
  &_description {
    color: #757575;
    font-size: 1.125rem;
    font-weight: 300;
    margin-bottom: 30px;
  }
  &_date {
    color: #757575;
    font-size: 1.125rem;
    font-weight: 300;
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 70px;
    @media (max-width: 480px) {
      margin-bottom: 20px;
    }
    &:before {
      content: '';
      width: 36px;
      min-width: 36px;
      height: 36px;
      display: block;
      background-image: url(../images/sprite.svg#calendar);
      background-size: cover;
      margin-right: 20px;
      top: 0;
      left: 0;
    }
  }
}

.step-img-wrap {
  @media(max-width: 1300px) {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.step-img {
  width: 50%;
  width: 800px;
  min-width: 800px;
  height: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  @media (max-width: 900px) {
    width: 400px;
    min-width: 400px;
    height: 400px;
  }
  @media (max-width: 480px) {
    width: 250px;
    min-width: 250px;
    height: 250px;
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    // display: none;
    // @media (max-width: 900px) {
    //   display: block;
    // }
  }
  &_number {
    font-size: 18px;
    color: #fff;
    background: #DAE0E3;
    border-radius: 50%;
    width: 35px;
    min-width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    transition: all 0.2s ease-in-out;
    @media (max-width: 480px) {
      font-size: 10px;
      width: 20px;
      min-width: 20px;
      height: 20px;
      margin-right: 10px;
    }
    &.active {
      background-color: #3FACDC;
    }
  }
}
.step-img_pic {
  width: 600px;
  height: 600px;
  background-position: -0 -0;
  opacity: 0;
  transition: opacity 0.4s ease;
  @media (max-width: 900px) {
    width: 380px;
    height: 380px;
  }
  @media (max-width: 480px) {
    width: 240px;
    height: 240px;
  }
  &.view {
    animation: 0.8s steps(18) 0s startPic01;
    animation-fill-mode:forwards;
    opacity: 1;
    @media (max-width: 900px) {
      animation: 0.8s steps(18) 0s startPic01-900;
      animation-fill-mode:forwards;
    }
    @media (max-width: 480px) {
      animation: 0.8s steps(18) 0s startPic01-480;
      animation-fill-mode:forwards;
    }
  }
}

