      padding-left: 40px;
      padding-right: 40px;
      width: 100%;
    }
  }
  &_item {
    padding-right: 100px;
    opacity: 0;
    transform: translateY(20px);
    transition: all 0.4s ease;
    &.view {
      opacity: 1;
      transform: translateY(0);
    }
    @media (max-width: 1400px) {
      padding-right: 30px;
    }
    @media (max-width: 1024px) {
      margin-bottom: 40px;
      width: 40%;
      padding-right: 0;
    }
    @media (max-width: 480px) {
      width: 45%;
    }
