      min-width: 370px;
      @media (max-width: 1400px) {
        min-width: 320px;
        width: 320px;
        padding-right: 50px;
      }
      @media (max-width: 1024px) {
        min-width: 0;
        width: 100%;
        padding-right: 0;
      }
    }
  }
  &_number {
    color: #3FACDC;
    font-size: 36px;
    margin-bottom: 10px;
    @media (max-width: 480px) {
      font-size: 28px;
    }
    span {
      font-size: 64px;
      line-height: 64px;
      @media (max-width: 480px) {
        font-size: 48px;
        line-height: 48px;
      }
    }
  }
  &_title {
    color: #fff;
    font-size: 1rem;
    display: block;
    margin-bottom: 15px;
  }
  &_subtitle {
    color: #fff;
    opacity: 0.7;
    font-size: 10px;
    display: block;
  }
}


