.header {
  background-image: url(../images/header_bg.png);
  background-size: cover;
  background-repeat: repeat-x;
  height: 89px;
  position: absolute;
  width: 100%;
  z-index: 20;
  @media (max-width: 480px) {
    height: 50px;
  }
}
.header-inner {
  background: #fff;
  height: 89px;
  position: relative;
  width: 100%;
  z-index: 20;
  @media (max-width: 480px) {
    height: 50px;
  }
  .nav {
    padding-left: 10px;
  }
  .nav_item {
    @media (max-width: 1420px) {
      padding: 0 5px;
    }
  }
  .h-phone {
    @media (max-width: 1200px) and (min-width: 1024px) {
      display: none;
    }
    @media (max-width: 480px) {
      display: none;
    }
  }
  .header-wrap {
    @media (max-width: 1200px) {
      padding-left: 0;
    }
  }
}
.header-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding-left: 30px;
  padding-right: 30px;
  @media (max-width: 1200px) {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.home {
  padding-left: 28px;
  padding-right: 28px;
  height: 100%;
  display: flex;
  align-items: center;
  @media (max-width: 480px) {
    padding-left: 15px;
    padding-right: 15px;
  }
  &:before {
    content: '';
    width: 16px;
    height: 16px;
    display: block;
    background-image: url(../images/sprite.svg#home);
    background-repeat: no-repeat;
    background-position: center;
  }
  &:hover {
    background: #036590;
    &:before {
      background-image: url(../images/sprite.svg#home_hover);
    }
  }
}

.nav {
  flex-grow: 1;
  padding-left: 80px;
  @media (max-width: 1400px) {
    padding-left: 10px;
  }
  @media (max-width: 1024px) {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(3,101,144, 0.9);
    width: 100%;
    height: 100%;
    margin-top: 89px;
    padding-top: 60px;
    z-index: 110;
    opacity: 0;
    visibility: hidden;
    transition: all 0.2s ease;
    &.active {
      visibility: visible;
      opacity: 1;
    }
  }
  @media (max-width: 480px) {
    margin-top: 50px;
  }
  &_list {
    display: flex;
    @media (max-width: 1024px) {
      display: block;
    }
  }
  &_item {
    padding: 0 20px;
    @media (max-width: 1300px) {
      padding: 0 10px;
    }
    @media (max-width: 1200px) {
      padding: 0 6px;
    }
    @media (max-width: 1024px) {
      margin-bottom: 15px;
      padding: 0 20px;
    }
  }
  &_link {
    text-decoration: none;
    color: #000;
    font-size: 0.938rem;
    padding: 2px 5px;
    display: block;
    border-bottom: 2px solid transparent;
    @media (max-width: 1024px) {
      color: #fff;
      font-size: 16px;
      border: none;
    }
    &:hover {
      color: #116893;
      border-color: #036590;
      @media (max-width: 1024px) {
        color: #fff;
      }
    }
  }
}

.h-contacts {
  display: flex;
  align-items: center;
}
.h-phone {
  color: #000;
  font-size: 1rem;
  font-weight: 600;
  padding-left: 26px;
  position: relative;
  text-decoration: none;
  display: block;
  margin-right: 46px;
  @media (max-width: 1200px) {
    margin-right: 20px;
  }
  @media (max-width: 480px) {
    padding-top: 2px;
  }
  &:hover {
    color: #116893;
  }
  &:before {
    content: '';
    width: 18px;
    height: 18px;
    background: #116893;
    display: block;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: 0;
  }
  &:after {
    content: '';
    width: 8px;
    height: 8px;
    display: block;
    background-image: url(../images/sprite.svg#phone_white);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    left: 5px;
    top: 5px;
  }
}

.social-icons {
  display: flex;
}
.social-icon {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #036590;
  display: block;
  margin-right: 16px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  @media (max-width: 480px) {
    width: 20px;
    height: 20px;
    margin-right: 8px;
  }
  &:hover {
    background-color: darken(#036590, 10%);
  }
  &:last-child {
    margin-right: 0;
  }
  &:before {
    content: '';
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: block;
  }
  &.inst {
    &:before {
      width: 16px;
      height: 16px;
      background-image: url(../images/sprite.svg#inst_white);
      @media (max-width: 480px) {
        width: 11px;
        min-width: 11px;
        height: 11px;
      }
    }
  }
  &.youtube:before {
    width: 16px;
    height: 13px;
    background-image: url(../images/sprite.svg#youtube_white);
    @media (max-width: 480px) {
      width: 11px;
      height: 8px;
    }
  }
}



.nav-icon {
	cursor: pointer;
	margin-left: 20px;
	display: none;
  z-index: 120;
	@media (max-width: 1024px) {
		display: block;
	}
  @media (max-width: 480px) {
    margin-left: 12px;
  }
  &.active {
    .nav-icon_line {
      &:nth-child(1) {
        transform: rotate(45deg) translate(0px, 3px);
      }
      &:nth-child(2) {
        display: none;
      }
      &:nth-child(3) {
        transform: rotate(-45deg) translate(1px, -5px);
      }
    }
  }
	&_line {
		width: 22px;
		height: 2px;
		margin-bottom: 5px;
		background-color: #036590;
    transition: all 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    @media (max-width: 480px) {
      height: 1px;
      width: 20px;
      margin-bottom: 4px;
    }
		&:last-child {
      margin-bottom: 0;
    }
	}
}


.h-logo {
  display: block;
  text-decoration: none;
  background: #036590;
  width: 243px;
  height: 100%;
  padding: 32px 58px;
  @media (max-width: 1300px) {
    padding: 32px 40px;
    width: 200px;
  }
  @media (max-width: 480px) {
    padding: 15px 30px;
    width: 150px;
  }
}
